import { forwardRef, useEffect, useRef, useState } from 'react';
import { calculate_backtest_analysis_apy } from '../../../../../../helpers/graph';
import {
  numberToPercentage,
  timestampToUTCString,
} from '../../../../../../helpers/utilities';
import { LineGraph } from '../../../../Charts/LineGraph';
import './index.scss';
interface BackTestAnlysisMiniProps {
  color: any;
  data: any;
  details: boolean;
}

interface IPercent {
  return: number | null;
  apy: number | null;
}

interface dataInfo {
  name: string;
  title: string;
  value: number | null;
}

export const BackTestAnlysisMini = forwardRef<
  HTMLDivElement,
  BackTestAnlysisMiniProps
>(({ details = true, color, data = {}, ...props }, ref) => {
  const [width, setWidth] = useState(0);
  const parentRef = useRef(null);

  useEffect(() => {
    parentRef.current && setWidth(parentRef.current!.offsetWidth);
    // console.log(parentRef.current!.offsetWidth);
  }, [parentRef]);

  const percentage: IPercent =
    data && data.length > 0
      ? {
          return: data[data.length - 1].percentage,
          apy: calculate_backtest_analysis_apy(data),
        }
      : {
          return: null,
          apy: null,
        };

  const backtestData: dataInfo[] = [
    { name: 'Return', title: 'Backtest Return:', value: percentage.return },
    { name: 'APY', title: 'Annualized Return:', value: percentage.apy },
  ];

  let displayData = (dataInfo: dataInfo[]) =>
    dataInfo.map((item, i) => {
      return (
        <div key={i} className="center">
          <div style={{ color: '#CAD2D8', marginBottom: '8px' }}>
            {item.title}
          </div>
          <div
            className={`medium${
              item.value === null
                ? ''
                : item.value >= 0
                ? ' positive'
                : ' negative'
            }`}
          >
            {item.name === 'Return' &&
              (item.value === null ? '' : item.value > 0 ? '+' : '')}
            {item.value === null ? '-' : numberToPercentage(item.value)}
          </div>
        </div>
      );
    });

  return (
    <div ref={parentRef} {...props} className="backtest-anlysis">
      {details && (
        <>
          <div className="headline-5 medium" style={{ color: '#FFFFFF' }}>
            Backtest Analysis
          </div>
          <div className="grid2-mini">{displayData(backtestData)}</div>
        </>
      )}
      <div className="line-graph-container">
        <div className="date-title headline-5 center">
          {data && data.length > 0
            ? `${timestampToUTCString(
                data[0].timestamp_sec,
                'MonthYear'
              )} - ${timestampToUTCString(
                data[data.length - 1].timestamp_sec,
                'MonthYear'
              )}`
            : ''}
        </div>
        <LineGraph
          color={color}
          data={data}
          type="mini"
          fill
          lineCount={1}
          tooltipInfo={{
            day: false,
            hours: true,
            value: [
              {
                color: '#19EE94',
                text: 'Return',
                value: 'percentage',
                suffix: '%',
                colorChanging: true,
              },
            ],
          }}
        />
      </div>
    </div>
  );
});
