import Grid from '@mui/material/Grid';
import { NumberCounter } from '../../../../../';
import {
  numberParseLarge,
  numberToPercentage,
} from '../../../../../../../helpers/utilities';
import { QuestionMarkTooltip } from '../../../../../Modals/AssetModal/toolTip';
import './index.scss';

interface OverallInfoProps {
  holdings: number;
  profits: string[];
  apy_7days: number | null;
  leverage: number;
  date: string;
  tvl: number;
  capacity: number;
  threshold: number;
  pool: any;
}
export const OverallInfo = ({
  holdings = 0,
  profits = ['', '$--'],
  apy_7days = null,
  leverage = 0,
  date = '',
  tvl = 0,
  capacity = 0,
  threshold = 0,
  pool = <></>,
}: OverallInfoProps) => {
  const percentage = tvl >= 0 && capacity > 0 ? tvl / capacity : 0;

  const ProgressBar = () => (
    <div style={{ position: 'relative' }}>
      {tvl >= 0 ? (
        <NumberCounter
          value={tvl}
          formatNumberFunction={(value) => `$${numberParseLarge(value, 2)}`}
        />
      ) : (
        '-'
      )}
      /
      {capacity >= 0 ? (
        <NumberCounter
          value={capacity}
          formatNumberFunction={(value) => numberParseLarge(value, 2)}
        />
      ) : (
        '-'
      )}{' '}
      <div className="overall-bar-percentage">
        {capacity > 0 ? (
          <NumberCounter
            value={tvl / capacity}
            formatNumberFunction={(value) =>
              ` ${numberToPercentage(Number(value))}`
            }
          />
        ) : (
          '-'
        )}
      </div>
      <div
        className="overall-bar"
        style={{
          display: 'grid',
          marginBottom: '24px',
          gridTemplateColumns:
            tvl > 0 && percentage > 0.0001
              ? percentage > 0.01
                ? `${percentage * 100}% ${(1 - percentage) * 100}%`
                : '1% 99%'
              : `100%`,
        }}
      >
        {tvl > 0 && percentage > 0.0001 && <div className="bar-left" />}
        {tvl < capacity && <div className="bar-right" />}
      </div>
    </div>
  );

  return (
    <Grid container className="overrall-info-container">
      <Grid item container className="data-grid-left" direction={'column'}>
        <Grid item>
          <div className="overallInfo-title subtitle-2">My Holdings</div>
          <div className="overallInfo-content headline-3">
            {Number.isNaN(holdings) ? (
              '$--'
            ) : (
              <NumberCounter
                value={holdings}
                formatNumberFunction={(value) =>
                  `$${numberParseLarge(value, 2)}`
                }
              />
            )}
          </div>
        </Grid>
        <Grid item>
          <div className="overallInfo-title subtitle-2">Profits</div>
          <div className={`overallInfo-content headline-5${profits[0]}`}>
            {profits[1]}
          </div>
        </Grid>
      </Grid>
      <Grid item container className="data-grid-right">
        <Grid item container direction={'column'}>
          <Grid item>
            <div className="overallInfo-title subtitle-2">TVL / Capacity</div>
            <div className="overallInfo-content headline-5">
              <ProgressBar />
            </div>
          </Grid>
          <Grid item>
            <div className="overallInfo-title subtitle-2">
              Underlying Farming Pool
            </div>
            <div className="overallInfo-content headline-5">{pool}</div>
          </Grid>
        </Grid>
        <Grid item container direction={'column'}>
          <Grid item xl={3} lg={3} md={3}>
            <div className="overallInfo-title subtitle-2">
              APY{' '}
              <QuestionMarkTooltip
                title={`This projected APY is based on past 7-day returns, excluding any occasional rebalancing costs.`}
                placementPC={'bottom-start'}
                toolTipClassName={'apy-bottom-start-tooltip'}
              />
            </div>
            <div className="overallInfo-content headline-5">
              {apy_7days !== null ? (
                <NumberCounter
                  value={apy_7days}
                  formatNumberFunction={(value) => numberToPercentage(value)}
                />
              ) : (
                '-'
              )}
            </div>
          </Grid>
          <Grid item xl={3} lg={3} md={3}>
            <div className="overallInfo-title subtitle-2">Target Leverage</div>
            <div className="overallInfo-content headline-5">
              {leverage ? (
                <NumberCounter
                  value={leverage}
                  formatNumberFunction={(value) => `${value}X`}
                />
              ) : (
                '-'
              )}
            </div>
          </Grid>
        </Grid>
        <Grid item container direction={'column'}>
          <Grid item>
            <div className="overallInfo-title subtitle-2">
              {' '}
              Vault Inception Date
            </div>
            <div className="overallInfo-content headline-5">
              {date === '' ? '--' : date}
            </div>
          </Grid>
          <Grid item>
            <div className="overallInfo-title subtitle-2">
              {`Price Movement Rebalance Threshold`}
            </div>
            <div className="overallInfo-content headline-5">
              {threshold !== null && threshold !== undefined ? (
                <NumberCounter
                  value={threshold}
                  formatNumberFunction={(value) =>
                    numberToPercentage(Number(value))
                  }
                />
              ) : (
                '-'
              )}
            </div>
          </Grid>
        </Grid>
      </Grid>
    </Grid>
  );
};
